@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@500&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family:     'IBM Plex Sans', sans-serif;

}

:root{
    --mainColor:#29335C;
    --mainColorLighter: #536096;
    --secondaryColor: #DB2B39;
    --textColor: #eee;
}

header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    padding: 0 2rem;
    background-color: var(--mainColor);
    color: var(--textColor);
    font-size: 1.4rem;
}
Footer {
    display: flex;
    align-items: center;
    background-color: var(--mainColor);
    padding: 0 2rem;
    height: 80px;
    font-size: 0.8rem;
    color: var(--textColor);
    justify-content: space-between;
}

nav a{
    margin: 0 2rem;
    color: var(--textColor);
    text-decoration: none;
}

nav a:hover{
    color: var(--secondaryColor);
}

header .nav-btn {
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--textColor);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
}
.home-page{
    display: flex;
    padding: 10px;
    align-items: center;
    justify-content: center;
    background-color: #8bb6af41;
    margin-left: 5rem;
    margin-right: 5rem;
    height: 200vh;
}
.home-image{
    max-width: 30%;
    position: absolute;
    top: 7rem;
    right: 7rem;
}
.scaleImages{
    max-width:100%;
    height:auto;
}

@media only screen and (max-width: 970px){
    header .nav-btn {
        visibility: visible;
        opacity: 1;
    }

    header nav{
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1.5rem;
        background-color: var(--mainColor);
        transition: .3s;
        transform: translateY(-100vh);
    }

    header .responsive_nav{
        transform: none;
    }

    nav .nav-close-btn{
        position: absolute;
        top: 2rem;
        right: 2rem;
    }

    nav a{
        font-size: 1.5rem;
    }
}